// let drawFn = new DrawFn(option);
window.onload = function (params) {
    
    // define global variable
    var getCss = function(o,key){
        return o.currentStyle? o.currentStyle[key] : document.defaultView.getComputedStyle(o,false)[key];   
    };
    function drag(element){
        element.style.top = getCss(element,"top");
        element.style.left = getCss(element,"left");
        var hammer = new Hammer(element);//hammer
        var x = 0;
        var y = 0;
        hammer.get('pinch').set({ enable: true });
        hammer.on("pinchmove pinchstart pinchin pinchout",e => {
            if(e.type == "pinchstart"){
                scaleIndex = transform.scale || 1;
            }
            transform.scale = scaleIndex * e.scale;
            img.style.transform = "scale(" + (scaleIndex * e.scale)+ ")";
        });

    }
    var myElement = document.getElementById("canvas");
    drag(myElement);
}

